function getSalePackageHeaderConfig(saleList, title, subTitle) {
  var hasOpen = false;
  saleList.forEach(function (item) {
    if (item.selected && item.showPrice >= 0) {
      hasOpen = true;
    }
  });
  return hasOpen ? {
    title: title || "出行保障",
    subTitle: "",
    color: "#00B87A",
    image: "http://pic.c-ctrip.com/pic/bus/resource/app/book_sale_tip_sel.png"
  } : {
    title: title || "添加保障",
    subTitle: "",
    color: "#FF7929",
    image: "http://pic.c-ctrip.com/pic/bus/resource/app/book_sale_tip.png"
  };
}
